import React from "react";
import Footer from "../Footer/Footer";
import Sidebar from "../Sidebar/Sidebar";
import Navbar from "../Navbar/Navbar";
import "./task1.css";

const Task1 = () => {
  return (
    <div>
      <Navbar />
      <article className="task1__description">
        <h1>Task 1: Nordea Fleet </h1>
        <p>
          The task is adding proper animation to the given design. In order to
          explore different approaches, 3 animations are made. In all of these
          animations, in order to provide a better user experience, the user
          will only be redirected to the campaign's URL by clicking on the
          button and not by clicking anywhere on the ad.
        </p>
      </article>
      <section className="sample__container">
        <article className="task1__description sample1">
          <h2>Animation 1</h2>
          <p>
            In this video each image of the car is pulled down and this movement
            is pulling down the price tag. In order to grab attention of the
            viewers the button is scaling up.
          </p>
        </article>
        <iframe
          className="sample-1"
          title="static_html"
          src="https://negarlatifian.github.io/task1-1/"
        ></iframe>
        <a
          className="task1__button"
          href="https://github.com/negarlatifian/task1-1"
        >
          View Code
        </a>
        <article className="task1__description sample2">
          <h2>Animation 2</h2>
          <p>
            This animation is more playful and gives the users a more energetic
            vibe.
          </p>
        </article>
        <iframe
          className="sample-2"
          title="static_html"
          src="https://negarlatifian.github.io/task1-02/"
        ></iframe>
        <a
          className="task1__button"
          href="https://github.com/negarlatifian/task1-02"
        >
          View Code
        </a>
        <article className="task1__description sample3">
          <h2>Animation 3</h2>
          <p>
            This animation is inspired by the old poster stands in the streets
            which are made of a roll of different posters attached to each other
            vertically and they move evenly from top to bottom.
          </p>
        </article>
        <iframe
          className="sample-3"
          title="static_html"
          src="https://negarlatifian.github.io/task1-03/"
        ></iframe>
        <a
          className="task1__button"
          href="https://github.com/negarlatifian/task1-03"
        >
          View Code
        </a>
      </section>
      <Sidebar />
      <Footer />
    </div>
  );
};

export default Task1;
