import React, { useRef, useEffect } from "react";
import Footer from "../Footer/Footer";
import Sidebar from "../Sidebar/Sidebar";
import Navbar from "../Navbar/Navbar";
import "./task3.css";

const Task3 = () => {
  const ref = useRef(null);

  const clickHandler = () => {
    // console.log(ref.current.contentWindow.window.location.reload());
    // ref.current.contentWindow.window.location.reload();
    window.location.reload();
  };

  useEffect(() => {
    const el2 = ref.current;
    console.log(el2);
  }, []);

  return (
    <div>
      <Navbar />
      <section className="task3__container">
        <article className="task3__description">
          <h1>Task 3: Nordea Video </h1>
          <p>
            As required in the task the polite loading seeting has been added to
            the code and in case of low speed internet connection and at the end
            of the animation, the fallback image substitutes the video
          </p>
          <div className="buttons__container">
            <a
              className="task2__button"
              href="https://github.com/negarlatifian/task3"
            >
              View Code
            </a>
            <a className="task2__button" onClick={clickHandler}>
              Reload
            </a>
          </div>
        </article>
        <iframe
          ref={ref}
          className="task3"
          title="static_html"
          src="https://negarlatifian.github.io/task3/"
        ></iframe>
      </section>
      <Sidebar />
      <Footer />
    </div>
  );
};

export default Task3;
