import React from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <nav className="navbar">
      <img
        src="https://res.cloudinary.com/dcxgripfi/image/upload/v1652966559/group1_kjc23s.png"
        className="navabar__logo"
        alt="logo"
      />
      <nav className="navButtons__container">
        <Link to="/" className="navbar__button">
          Home
        </Link>
        <Link to="/Task1" className="navbar__button">
          Task1
        </Link>
        <Link to="/Task2" className="navbar__button">
          Task2
        </Link>
        <Link to="/Task3" className="navbar__button">
          Task3
        </Link>
      </nav>
    </nav>
  );
};

export default Navbar;
